.bubbleCard_container {
  position: absolute;
  width: 100%;
  background: var(--background_primary);
  /* display: grid; */
  /* align-items: center; */
  /* left: 0; */
  top: 7%;
  border-radius: 0 0 1rem 1rem;
  transition: 0.2s ease-in-out;
}
.opacity_on_bubbleCard {
  opacity: 1;
  z-index: 350;
}
.opacity_off_bubbleCard {
  opacity: 0;
  z-index: 0;
}
.icon_bubbleCard {
  /* background: transparent; */
  font-size: 2rem;
  cursor: pointer;
  outline: none;
  color: var(--text_secondary);
  position: absolute;
  top: 1rem;
  right: 1rem;
}
.close_icon_bubbleCard {
  color: var(--text_secondary);
}
.bubbleCard-menu {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 80px);
  text-align: center;
  align-items: center;
  font-size: 1.3rem;
  /* margin-bottom: 4rem; */
  color: var(--text_secondary);
}

.bubbleCard-Genres {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.bubbleCard-Platforms {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.bubbleCard-Stores {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.bubbleCard-CreatedinDB {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.icon_bubbleCard > span {
  position: absolute;
  top: -5.2rem;
}

@media screen and (max-width: 480px) {
  .bubbleCard-menu {
    grid-template-rows: repeat(4, 60px);
  }
}
